s/form';\n@import 'placeholders/heading';\n@import 'placeholders/link';\n\n// Admin\n@import 'admin/addons';\n@import 'admin/box';\n@import 'admin/grid';\n@import 'admin/layout';\n@import 'admin/license';\n@import 'admin/nav';\n@import 'admin/status';\n\n.toplevel_page_chipmunk #wpcontent {\n  padding-left: 0;\n}\n\n[id*='accordion-section-title'] {\n  margin-top: 1.5rem;\n}\n","/*\n** Setup - Variables\n** -------------------------------------------------------------------------- */\n\n// SASS display values\n$display-values: (inline, inline-block, block, flex) !default;\n\n// SASS breakpoints\n$breakpoints: (\n  sm: 0,\n  md: 680px,\n  lg: 1024px,\n) !default;\n\nbody {\n  /* Colors\n  ------------------------------------- */\n  --chipmunk--color--primary: #5b5c89;\n  --chipmunk--color--black: #1d1d1d;\n  --chipmunk--color--gray-dark: #444;\n  --chipmunk--color--gray: #777;\n  --chipmunk--color--gray-light: #cdcdcd;\n  --chipmunk--color--gray-lighter: #e6e6e6;\n  --chipmunk--color--gray-lightest: #ededed;\n  --chipmunk--color--white: #fafafa;\n  --chipmunk--color--green: #e6efe7;\n  --chipmunk--color--creme: #f5f3ed;\n\n  // Status colors\n  --chipmunk--color--status-success: #4f8a10;\n  --chipmunk--color--status-warning: #9f6000;\n  --chipmunk--color--status-info: #369bf8;\n  --chipmunk--color--status-error: #f74444;\n\n  // Customizable colors\n  --chipmunk--color--link: var(--chipmunk--color--primary);\n  --chipmunk--color--background: var(--chipmunk--color--gray-lightest);\n  --chipmunk--color--section: var(--chipmunk--color--white);\n\n  /* Layers\n  * ---------------------------------- */\n  --chipmunk--layer--negative: -1;\n  --chipmunk--layer--neutral: 0;\n  --chipmunk--layer--base: 1;\n  --chipmunk--layer--utils: 5;\n  --chipmunk--layer--dropdown: 15;\n  --chipmunk--layer--overlay: 20;\n  --chipmunk--layer--head: 25;\n  --chipmunk--layer--top: 30;\n  --chipmunk--layer--search: 35;\n  --chipmunk--layer--popup: 40;\n\n  /* Typography\n  ------------------------------------- */\n  // Fonts\n  --chipmunk--font--system:\n    -apple-system, blinkmacsystemfont, 'Segoe UI', roboto, oxygen, ubuntu, cantarell,\n    'Open Sans', 'Helvetica Neue', sans-serif;\n  --chipmunk--font--mono: menlo, consolas, monaco, monospace;\n\n  // Typography\n  --chipmunk--typography--font-family: var(--chipmunk--font--system);\n  --chipmunk--typography--font-size: 1.6rem;\n  --chipmunk--typography--line-height: 1.618;\n\n  // Headings\n  --chipmunk--typography--scale: 1.25;\n  --chipmunk--typography--small: var(--chipmunk--typography--font-size);\n  --chipmunk--typography--normal: var(--chipmunk--typography--small) * var(--chipmunk--typography--scale);\n  --chipmunk--typography--medium: var(--chipmunk--typography--normal) * var(--chipmunk--typography--scale);\n  --chipmunk--typography--large: var(--chipmunk--typography--medium) * var(--chipmunk--typography--scale);\n  --chipmunk--typography--xlarge: var(--chipmunk--typography--large) * var(--chipmunk--typography--scale);\n  --chipmunk--typography--huge: var(--chipmunk--typography--xlarge) * var(--chipmunk--typography--scale);\n  --chipmunk--typography--heading-font-family: var(--chipmunk--font--system);\n\n  // Content\n  --chipmunk--typography--content-size: 1.25em;\n\n  /* Layout\n  ------------------------------------- */\n  --chipmunk--layout--container-width: 110rem;\n  --chipmunk--layout--container-gutter: clamp(2.4rem, 5vw, 7.2rem);\n  --chipmunk--layout--column-gutter: calc(var(--chipmunk--layout--container-width) * 0.025);\n  --chipmunk--layout--content-width: 8;\n  --chipmunk--layout--popup-width: 80rem;\n  --chipmunk--layout--tile-padding: 1rem;\n\n  /* Buttons\n  ------------------------------------- */\n  --chipmunk--button--border-width: 1px;\n  --chipmunk--button--border-radius: 100vmin;\n  --chipmunk--button--font-size: 0.875em;\n\n  /* Miscellaneous\n  ------------------------------------- */\n  // Transitions\n  --chipmunk--transition-duration: 0.25s;\n\n  // Spacers\n  --chipmunk--spacer: 1.25em;\n\n  // Borders\n  --chipmunk--border-opacity: 0.075;\n  --chipmunk--border-radius: 0.5rem;\n\n  // Logo\n  --chipmunk--logo-height: 4rem;\n}\n","/*\n** Setup - Animations\n** -------------------------------------------------------------------------- */\n\n@keyframes button-spin {\n  from {\n    transform: rotate(0);\n  }\n\n  to {\n    transform: rotate(360deg);\n  }\n}\n\n@keyframes grow {\n  0%,\n  100% {\n    transform: translate3d(0, 0, 0) scale(1);\n  }\n\n  50% {\n    transform: translate3d(0, 0, 0) scale(1.5);\n  }\n}\n","/*\n** Tools - Align\n** -------------------------------------------------------------------------- */\n\n// Add negative spacing to make the element full width\n// @param {Boolean} $container  - Contain the max width\n// @param {Boolean} $gutters  - Add paddings at the sides\n@mixin align-full($container: false, $gutters: false) {\n  @include bp-down(lg) {\n    &.wp-block-image,\n    &.wp-block-cover,\n    &.wp-block-cover-image,\n    &.wp-block-gallery,\n    &.wp-block-embed,\n    &.wp-block-columns {\n      width: 100vw;\n      margin-left: 50%;\n      transform: translateX(-50%);\n    }\n  }\n\n  @include bp(lg) {\n    width: calc(100vw #{if($gutters, '- calc(var(--chipmunk--layout--container-gutter) * 2)', null)});\n    margin-left: 50%;\n    transform: translateX(-50%);\n\n    @if ($container) {\n      max-width: var(--chipmunk--layout--container-width);\n    }\n\n    @else {\n      max-width: 256rem;\n    }\n  }\n}\n\n// Add negative spacing to make the element\n@function negative-margin() {\n  @return calc(\n    ((12 - var(--chipmunk--layout--content-width)) / 2) / var(--chipmunk--layout--content-width) * 100% * -1\n  );\n}\n","/*\n** Tools - Breakpoints\n** -------------------------------------------------------------------------- */\n\n@use 'sass:map';\n@use 'sass:list';\n\n@function bp-val($name) {\n  @if type-of($name) == 'number' {\n    @return $name;\n  }\n\n  $val: map.get($breakpoints, $name);\n\n  @if not $val {\n    @error 'Breakpoint #{$name} not found';\n  }\n\n  @return $val;\n}\n\n@function bp-val-next($name) {\n  $breakpoint-names: map_keys($breakpoints);\n  $n: list.index($breakpoint-names, $name);\n\n  @if $n >= length($breakpoint-names) {\n    @error 'Next breakpoint after #{$name} not found';\n  }\n\n  $next-key: list.nth($breakpoint-names, $n + 1);\n\n  @return map.get($breakpoints, $next-key);\n}\n\n@mixin bp($name) {\n  $val: bp-val($name);\n\n  @media (min-width: $val) {\n    @content;\n  }\n}\n\n@mixin bp-down($name) {\n  $val: bp-val($name);\n\n  @media (max-width: ($val - 0.02)) {\n    @content;\n  }\n}\n\n@mixin bp-only($name) {\n  @if type-of($name) == 'number' {\n    @error '#{$name} should be breakpoint name';\n  }\n\n  $min: bp-val($name);\n  $max: bp-val-next($name);\n\n  @media (min-width: $min) and (max-width: ($max - 0.02)) {\n    @content;\n  }\n}\n\n@mixin bp-between($name1, $name2) {\n  $min: bp-val($name1);\n  $max: bp-val($name2);\n\n  @media (min-width: $min) and (max-width: ($max - 0.02)) {\n    @content;\n  }\n}\n","/*\n** Tools - Cover\n** -------------------------------------------------------------------------- */\n\n@mixin cover($alpha: 0.1, $color: rgba(0, 0, 0)) {\n  &::after {\n    content: '';\n    position: absolute;\n    inset: 0;\n    background-image: linear-gradient(rgba($color, 0), rgba($color, $alpha));\n    pointer-events: none;\n  }\n}\n","/*\n** Tools - Dropdown\n** -------------------------------------------------------------------------- */\n\n@mixin dropdown($margin, $theme: 'dark', $position: 'left', $offset: 1.5rem) {\n  position: absolute;\n  top: calc(100% + #{$offset} - 1px);\n  z-index: var(--chipmunk--layer--dropdown);\n  display: flex;\n  flex-direction: column;\n  padding: 0.75em 1em;\n  gap: 0.5em;\n  background-color: if($theme == 'dark', var(--chipmunk--color--black), var(--chipmunk--color--section));\n  border-radius: var(--chipmunk--border-radius);\n  border: 1px solid if($theme == 'dark', transparent, var(--chipmunk--color--gray-lighter));\n  color: if($theme == 'dark', var(--chipmunk--color--section), var(--chipmunk--color--black));\n  font-size: 1.4rem;\n  opacity: 0;\n  pointer-events: none;\n  transform: translate3d(0, #{$offset * 0.5}, 0);\n  transition-property: opacity, transform;\n  transition-duration: var(--chipmunk--transition-duration);\n\n  @if ($position == 'left') {\n    left: 0;\n    text-align: left;\n  }\n\n  @if ($position == 'right') {\n    right: 0;\n    text-align: right;\n  }\n\n  &::before,\n  &::after {\n    content: '';\n    display: block;\n    position: absolute;\n    bottom: 100%;\n  }\n\n  &::before {\n    height: $offset;\n    left: 0;\n    right: 0;\n  }\n\n  &::after {\n    border: 0.75rem solid transparent;\n    border-bottom-color: if($theme == 'dark', var(--chipmunk--color--black), var(--chipmunk--color--section));\n    filter: drop-shadow(0 -1px 0 if($theme == 'dark', transparent, var(--chipmunk--color--gray-lighter)));\n\n    @if ($position == 'left') {\n      left: $margin;\n    }\n\n    @if ($position == 'right') {\n      right: $margin;\n    }\n  }\n\n  [class*='link'] {\n    position: relative;\n    text-align: inherit;\n\n    &:hover {\n      opacity: 0.8;\n    }\n  }\n\n  ul {\n    top: calc(0.5em - #{$margin});\n\n    @if ($position == 'left') {\n      right: auto;\n      left: calc(100% + #{$offset} + 1em - 1px);\n    }\n\n    @if ($position == 'right') {\n      right: calc(100% + #{$offset} + 1em - 1px);\n      left: auto;\n    }\n\n    &::before {\n      height: auto;\n      width: calc(#{$offset} + 1em);\n      top: 0;\n      bottom: 0;\n\n      @if ($position == 'left') {\n        right: 100%;\n        left: auto;\n      }\n\n      @if ($position == 'right') {\n        right: auto;\n        left: 100%;\n      }\n    }\n\n    &::after {\n      border-bottom-color: transparent;\n      bottom: auto;\n      top: $margin;\n\n      @if ($position == 'left') {\n        left: -$offset;\n        border-right-color: if($theme == 'dark', var(--chipmunk--color--black), var(--chipmunk--color--section));\n        filter: drop-shadow(-1px 0 0 if($theme == 'dark', transparent, var(--chipmunk--color--gray-lighter)));\n      }\n\n      @if ($position == 'right') {\n        right: -$offset;\n        border-left-color: if($theme == 'dark', var(--chipmunk--color--black), var(--chipmunk--color--section));\n        filter: drop-shadow(1px 0 0 if($theme == 'dark', transparent, var(--chipmunk--color--gray-lighter)));\n      }\n    }\n  }\n}\n\n@mixin dropdown-open {\n  opacity: 1;\n  pointer-events: all;\n  transform: translate3d(0, 0, 0);\n}\n","/*\n** Tools - Line clamp\n** -------------------------------------------------------------------------- */\n\n@mixin line-clamp($lines) {\n  display: -webkit-box; /* stylelint-disable-line value-no-vendor-prefix */\n  -webkit-box-orient: vertical; /* stylelint-disable-line property-no-vendor-prefix */\n  -webkit-line-clamp: $lines;\n  overflow: hidden;\n}\n","/*\n** Tools - Loader\n** -------------------------------------------------------------------------- */\n\n@mixin loader($size: 1em) {\n  > span {\n    transition: opacity var(--chipmunk--transition-duration);\n  }\n\n  &.is-loading {\n    position: relative;\n\n    > span {\n      opacity: 0;\n    }\n\n    &::after {\n      position: absolute;\n      content: '';\n      top: 50%;\n      left: 50%;\n      margin-top: $size * -0.5;\n      margin-left: $size * -0.5;\n      width: $size;\n      height: $size;\n      border-radius: 50%;\n      border-color: currentcolor transparent transparent;\n      border-style: solid;\n      border-width: 1px;\n      box-shadow: 0 0 0 1px transparent;\n      animation: button-spin 0.6s linear;\n      animation-iteration-count: infinite;\n    }\n  }\n}\n","/*\n** Tools - Spacers\n** -------------------------------------------------------------------------- */\n\n// Adds spacer to the element\n@mixin _spacer($value, $direction, $property) {\n  @if ($property == margin) {\n    @if index(top left, $direction) {\n      &:not(:first-child) {\n        #{$property}-#{$direction}: $value;\n      }\n    }\n\n    @else {\n      &:not(:last-child) {\n        #{$property}-#{$direction}: $value;\n      }\n    }\n  }\n\n  @else {\n    #{$property}-#{$direction}: $value;\n  }\n}\n\n// Adds relative spacer to the element\n@function spacer-relative($size: 1) {\n  @return calc(var(--chipmunk--typography--line-height) * $size * 1em);\n}\n\n@mixin spacer-relative($size: 1, $direction: 'top', $property: 'margin') {\n  @include _spacer(spacer-relative($size), $direction, $property);\n}\n\n// Adds fixed spacer to the element\n@function spacer-fixed($size: 1) {\n  @return calc(var(--chipmunk--spacer) * #{$size});\n}\n\n@mixin spacer-fixed($size: 1, $direction: 'top', $property: 'margin') {\n  @include _spacer(spacer-fixed($size), $direction, $property);\n}\n\n// Adds clamped spacer to the element\n@function spacer-clamped($size: 1, $scale: 1vw) {\n  $clamp-min: calc(var(--chipmunk--spacer) * #{$size * 0.5});\n  $clamp-max: calc(var(--chipmunk--spacer) * #{$size});\n\n  @return clamp(\n    #{if($size < 0, $clamp-max, $clamp-min)},\n    #{$size} * #{$scale},\n    #{if($size < 0, $clamp-min, $clamp-max)}\n  );\n}\n\n@mixin spacer-clamped($size: 1, $direction: 'top', $property: 'margin', $scale: 1vw) {\n  @include _spacer(spacer-clamped($size, $scale), $direction, $property);\n}\n","/*\n** Tools - Typography\n** -------------------------------------------------------------------------- */\n\n@use 'sass:map';\n\n@mixin typography-properties($typography, $excluded: null) {\n  $properties: color, font-family, font-size, font-weight, font-style, line-height, letter-spacing, text-transform;\n\n  @each $property in $properties {\n    @if map.has-key($typography, $property) and not index($excluded, $property) {\n      #{$property}: map.get($typography, $property);\n    }\n  }\n}\n","/*\n** Placeholders - Box\n** -------------------------------------------------------------------------- */\n\n%bordered-box {\n  position: relative;\n  z-index: var(--chipmunk--layer--base);\n  box-shadow:\n    0 1px 0 0 rgba(0 0 0 / var(--chipmunk--border-opacity)),\n    0 -1px 0 0 rgba(0 0 0 / var(--chipmunk--border-opacity));\n}\n","/*\n** Placeholders - Button\n** -------------------------------------------------------------------------- */\n\n%button {\n  display: inline-block;\n  padding: 0.5em 1.75em;\n  background-color: transparent;\n  border: var(--chipmunk--button--border-width) solid transparent;\n  font-size: var(--chipmunk--button--font-size);\n  line-height: normal;\n  text-align: center;\n  white-space: nowrap;\n}\n\n%button-rounded {\n  border-radius: var(--chipmunk--button--border-radius);\n}\n\n%button-squared {\n  border-radius: 0;\n}\n\n%button-primary {\n  background-color: var(--chipmunk--color--primary);\n  color: var(--chipmunk--color--white);\n\n  &:not([disabled]):hover {\n    background-color: transparent;\n    border-color: currentcolor;\n    color: var(--chipmunk--color--primary);\n  }\n}\n\n%button-primary-outline {\n  background-color: transparent;\n  border-color: currentcolor;\n  color: var(--chipmunk--color--primary);\n\n  &:not([disabled]):hover {\n    background-color: var(--chipmunk--color--primary);\n    border-color: transparent;\n    color: var(--chipmunk--color--white);\n  }\n}\n\n%button-secondary {\n  background-color: var(--chipmunk--color--gray);\n  color: var(--chipmunk--color--white);\n\n  &:not([disabled]):hover {\n    background-color: transparent;\n    border-color: currentcolor;\n    color: var(--chipmunk--color--gray);\n  }\n}\n\n%button-secondary-outline {\n  background-color: transparent;\n  border-color: currentcolor;\n  color: var(--chipmunk--color--gray);\n\n  &:not([disabled]):hover {\n    background-color: var(--chipmunk--color--gray);\n    border-color: transparent;\n    color: var(--chipmunk--color--white);\n  }\n}\n\n%button-white {\n  background-color: var(--chipmunk--color--white);\n  color: var(--chipmunk--color--white);\n\n  &:not([disabled]):hover {\n    background-color: transparent;\n    border-color: currentcolor;\n    color: var(--chipmunk--color--white);\n  }\n}\n\n%button-white-outline {\n  background-color: transparent;\n  border-color: currentcolor;\n  color: var(--chipmunk--color--white);\n\n  &:not([disabled]):hover {\n    background-color: var(--chipmunk--color--white);\n    border-color: transparent;\n    color: var(--chipmunk--color--primary);\n  }\n}\n","/*\n** Placeholders - Caption\n** -------------------------------------------------------------------------- */\n\n%caption {\n  margin: spacer-clamped(1) 0 0;\n  color: var(--chipmunk--color--gray);\n  font-size: 0.875em;\n  word-wrap: break-word;\n}\n\n%caption-indented {\n  margin-left: var(--chipmunk--layout--container-gutter);\n  margin-right: var(--chipmunk--layout--container-gutter);\n}\n\n%caption-quote {\n  @include spacer-relative(1);\n\n  display: inline-block;\n\n  &::before {\n    content: ' ';\n  }\n}\n","/*\n** Placeholders - Container\n** -------------------------------------------------------------------------- */\n\n%container {\n  width: 100%;\n  margin-right: auto;\n  margin-left: auto;\n  max-width: min(var(--chipmunk--layout--container-width), calc(100vw - var(--chipmunk--layout--container-gutter) * 2));\n}\n","/*\n** Placeholders - Copy\n** -------------------------------------------------------------------------- */\n\n%copy {\n  h1,\n  h2,\n  h3,\n  h4,\n  h5,\n  h6 {\n    @include spacer-relative;\n\n    @extend %heading;\n\n    + ul:not(:first-child),\n    + ol:not(:first-child) {\n      margin-top: 0;\n    }\n  }\n\n  h1 {\n    @extend %heading-huge !optional;\n  }\n\n  h2 {\n    @extend %heading-xlarge !optional;\n  }\n\n  h3 {\n    @extend %heading-large !optional;\n  }\n\n  h4 {\n    @extend %heading-medium !optional;\n  }\n\n  h5 {\n    @extend %heading-normal !optional;\n  }\n\n  h6 {\n    @extend %heading-small !optional;\n  }\n\n  p,\n  ul,\n  ol,\n  blockquote,\n  table,\n  pre,\n  code,\n  iframe,\n  .wp-caption {\n    &:not([class*='block']) {\n      @include spacer-relative;\n    }\n  }\n\n  ul:not([class*='block']),\n  ol:not([class*='block']) {\n    @include spacer-relative(1, left, padding);\n\n    list-style-position: inside;\n\n    li {\n      @include spacer-relative(0.25);\n    }\n\n    ul,\n    ol {\n      @include spacer-relative(0.25, top, padding);\n      @include spacer-relative(1, left, padding);\n    }\n  }\n\n  ul:not([class*='block']) {\n    list-style: disc;\n  }\n\n  ol:not([class*='block']) {\n    list-style: decimal;\n  }\n\n  a:not([class]) {\n    @extend %link;\n  }\n\n  iframe {\n    display: block;\n    width: 100%;\n    background-color: var(--chipmunk--color--white);\n  }\n\n  blockquote:not([class*='block']) {\n    p {\n      font-size: 1.2em;\n      line-height: 1.5;\n    }\n\n    em {\n      font-style: normal;\n    }\n\n    cite {\n      @extend %caption;\n    }\n  }\n\n  table {\n    min-width: 100%;\n    table-layout: fixed;\n    border: 1px solid var(--chipmunk--color--gray);\n    border-spacing: 0;\n    border-collapse: collapse;\n    font-size: 0.8em;\n\n    @include bp(md) {\n      border-width: 2px;\n    }\n\n    thead {\n      background-color: var(--chipmunk--color--white);\n      font-weight: bold;\n      text-align: left;\n    }\n\n    td,\n    th {\n      padding: 0.5em 1em;\n      border: 1px solid var(--chipmunk--color--gray);\n      vertical-align: top;\n\n      @include bp(md) {\n        border-width: 2px;\n      }\n    }\n  }\n\n  hr:not([class]) {\n    border-bottom: 2px solid var(--chipmunk--color--gray-lighter);\n  }\n\n  strong,\n  b {\n    font-weight: bold;\n  }\n\n  em,\n  i {\n    font-style: italic;\n  }\n\n  pre {\n    font-family: var(--chipmunk--font--mono);\n    font-size: 0.8125em;\n    line-height: 1.75;\n    white-space: nowrap;\n    overflow: auto;\n    tab-size: 4;\n  }\n\n  cite,\n  footer {\n    @extend %caption;\n    @extend %caption-quote;\n  }\n\n  figcaption {\n    @extend %caption;\n  }\n\n  .alignwide > figcaption,\n  .alignfull > figcaption {\n    @extend %caption-indented;\n  }\n\n  .alignwide > figcaption {\n    @include bp(lg) {\n      margin-left: 0;\n      margin-right: 0;\n    }\n  }\n}\n\n%copy-type {\n  font-size: var(--chipmunk--typography--content-size);\n}\n","/*\n** Placeholders - Form\n** -------------------------------------------------------------------------- */\n\n%input {\n  display: block;\n  width: 100%;\n  padding: 0.25em 0;\n  background-color: transparent;\n  border: 0;\n  border-bottom: 0.1em solid currentcolor;\n  border-radius: 0;\n  color: inherit;\n  font-size: inherit;\n  text-align: inherit;\n  resize: none;\n  appearance: none;\n\n  &:not([rows]) {\n    overflow: hidden;\n    text-overflow: ellipsis;\n    white-space: nowrap;\n  }\n\n  &::placeholder {\n    color: inherit;\n    opacity: 0.5;\n  }\n\n  &:focus {\n    outline: none;\n  }\n\n  &[type='number']::-webkit-inner-spin-button,\n  &[type='number']::-webkit-outer-spin-button {\n    appearance: none;\n  }\n\n  &:-webkit-autofill {\n    animation-delay: 1s;\n    animation-name: autofill;\n    animation-fill-mode: both;\n  }\n}\n","/*\n** Placeholders - Heading\n** -------------------------------------------------------------------------- */\n\n%heading {\n  font-family: var(--chipmunk--typography--heading-font-family);\n  font-weight: 700;\n  line-height: calc(1.5rem + 1.5ex);\n}\n\n%heading-small {\n  font-size: var(--chipmunk--typography--small);\n}\n\n%heading-normal {\n  font-size:\n    clamp(\n      var(--chipmunk--typography--small),\n      calc(1rem + var(--chipmunk--typography--normal--preferred, 1vw)),\n      var(--chipmunk--typography--normal)\n    );\n}\n\n%heading-medium {\n  font-size:\n    clamp(\n      var(--chipmunk--typography--normal),\n      calc(1rem + var(--chipmunk--typography--medium--preferred, 2vw)),\n      var(--chipmunk--typography--medium)\n    );\n}\n\n%heading-large {\n  font-size:\n    clamp(\n      var(--chipmunk--typography--medium),\n      calc(1rem + var(--chipmunk--typography--large--preferred, 3vw)),\n      var(--chipmunk--typography--large)\n    );\n}\n\n%heading-xlarge {\n  font-size:\n    clamp(\n      var(--chipmunk--typography--large),\n      calc(1rem + var(--chipmunk--typography--xlarge--preferred, 4vw)),\n      var(--chipmunk--typography--xlarge)\n    );\n}\n\n%heading-huge {\n  font-size:\n    clamp(\n      var(--chipmunk--typography--xlarge),\n      calc(1rem + var(--chipmunk--typography--huge--preferred, 5vw)),\n      var(--chipmunk--typography--huge)\n    );\n}\n","/*\n** Placeholders - Link\n** -------------------------------------------------------------------------- */\n\n%link {\n  &:not([class]) {\n    color: var(--chipmunk--color--link);\n\n    &:hover,\n    &:focus {\n      opacity: 0.8;\n    }\n  }\n}\n\n%link-transition {\n  color: inherit;\n  cursor: pointer;\n  text-decoration: none;\n  -webkit-user-drag: none;\n  transition-duration: var(--chipmunk--transition-duration);\n  transition-property: color, background-color, border-color, outline, box-shadow, opacity, outline, transform;\n\n  &:focus {\n    outline: none;\n  }\n}\n","/*\n** Admin / Addons\n** -------------------------------------------------------------------------- */\n\n.chipmunk__addon {\n  align-items: flex-start;\n  gap: 1.5em;\n  padding: 1.5em;\n}\n\n.chipmunk__addon-head {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  gap: 1.5em;\n}\n\n.chipmunk__addon-title {\n  margin: 0;\n}\n\n.chipmunk__addon-error {\n  display: flex;\n  align-items: center;\n  flex-wrap: wrap;\n  gap: 1em;\n  color: var(--chipmunk--color--status-error) !important;\n}\n","/*\n** Admin / Box\n** -------------------------------------------------------------------------- */\n\n.chipmunk__box {\n  display: flex;\n  flex-direction: column;\n  width: 100%;\n  background: #fff;\n  border: 1px solid #d6d6d6;\n  box-shadow: 0 2px 5px rgb(0 0 0 / 10%);\n  border-radius: 5px;\n  overflow: hidden;\n}\n","/*\n** Admin / Grid\n** -------------------------------------------------------------------------- */\n\n.chipmunk__grid {\n  display: grid;\n  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));\n  gap: clamp(1.5em, 2.5vw, 3em);\n}\n","/*\n** Admin / Layout\n** -------------------------------------------------------------------------- */\n\n.chipmunk {\n  * {\n    box-sizing: border-box;\n  }\n\n  h1,\n  h2,\n  h3,\n  ul,\n  ol,\n  li,\n  table {\n    &:not([class]) {\n      margin: 0;\n      padding: 0;\n      color: inherit;\n    }\n  }\n\n  table.form-table,\n  p.submit {\n    &:not(:first-child) {\n      margin-top: 1.5rem;\n    }\n  }\n\n  p {\n    color: inherit;\n  }\n\n  a {\n    text-decoration: none;\n    outline: none;\n  }\n}\n\n.chipmunk__wrap {\n  width: 100%;\n  margin: 0;\n  padding-right: clamp(1.5em, 2.5vw, 2.5em);\n  padding-left: clamp(1.5em, 2.5vw, 2.5em);\n}\n\n.chipmunk__head {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  background-color: var(--chipmunk--color--green);\n  color: var(--chipmunk--color--black);\n}\n\n.chipmunk__head,\n.chipmunk__main {\n  padding-top: 2em;\n  padding-bottom: 2em;\n}\n\n.chipmunk__title {\n  position: relative;\n  display: flex;\n  align-items: center;\n  gap: 0.5em;\n  margin: 0;\n  padding: 0;\n  font-size: 1.75em;\n  font-weight: 600;\n  color: inherit;\n\n  svg {\n    fill: currentcolor;\n    height: 1em;\n    width: 1em;\n    font-size: 1.25em;\n  }\n\n  > span {\n    position: absolute;\n    top: 0;\n    left: 100%;\n    transform: translate3d(-25%, -50%, 0);\n  }\n}\n\n.chipmunk__logo {\n  padding: 0.4em;\n  background-color: var(--chipmunk--color--white);\n  border-radius: 50%;\n  box-shadow: 0 1px 4px rgba(0 0 0 / 20%);\n}\n\n.chipmunk__main {\n  .notice {\n    margin: 0;\n\n    &:not(:last-child) {\n      margin-bottom: 2em;\n    }\n\n    + .notice {\n      margin-top: -1em;\n    }\n  }\n\n  h1,\n  h2,\n  h3 {\n    line-height: 1.5;\n  }\n}\n\n.chipmunk__hide {\n  border: 0 !important;\n  clip: rect(0 0 0 0) !important;\n  clip-path: inset(50%) !important;\n  height: 1px !important;\n  margin: -1px !important;\n  overflow: hidden !important;\n  padding: 0 !important;\n  position: absolute !important;\n  white-space: nowrap !important;\n  width: 1px !important;\n}\n","/*\n** Admin / License\n** -------------------------------------------------------------------------- */\n\n.chipmunk__license-head {\n  margin: 0;\n  padding: 1em;\n  width: auto;\n  background: var(--chipmunk--color--white);\n  border-bottom: 1px solid var(--chipmunk--color--gray-light);\n}\n\n.chipmunk__license-body {\n  padding: 1em;\n\n  input.regular-text {\n    width: 100%;\n    margin: 0;\n\n    &:not(:last-child) {\n      margin-bottom: 0.5em;\n    }\n  }\n}\n\n.chipmunk__license-data {\n  background: var(--chipmunk--color--white);\n  padding: 1em;\n  border-top: 1px solid var(--chipmunk--color--gray-lighter);\n\n  a {\n    color: inherit;\n    text-decoration: none;\n  }\n\n  &.is-valid {\n    color: var(--chipmunk--color--status-success);\n  }\n\n  &.is-expires-soon,\n  &.is-site_inactive,\n  &.is-item_name_mismatch {\n    color: var(--chipmunk--color--status-warning);\n  }\n\n  &.is-expired,\n  &.is-error,\n  &.is-missing,\n  &.is-invalid,\n  &.is-invalid_item_id {\n    color: var(--chipmunk--color--status-error);\n  }\n}\n","/*\n** Admin / Nav\n** -------------------------------------------------------------------------- */\n\n.chipmunk__nav {\n  display: flex;\n  flex-wrap: wrap;\n  align-items: flex-start;\n  gap: 2em;\n  padding-top: 1.5em;\n  background-color: var(--chipmunk--color--white);\n  border-top: 1px solid var(--chipmunk--color--gray-light);\n  border-bottom: 1px solid var(--chipmunk--color--gray-light);\n\n  a {\n    padding: 0.5em 0;\n    border-bottom: 3px solid transparent;\n    color: var(--chipmunk--color--gray);\n    font-size: 1.25em;\n    outline: none;\n\n    &:hover {\n      color: var(--chipmunk--color--gray-dark);\n    }\n\n    &.active {\n      border-bottom-color: currentcolor;\n      color: var(--chipmunk--color--black);\n    }\n  }\n}\n","/*\n** Admin / Status\n** -------------------------------------------------------------------------- */\n\n.chipmunk__status {\n  display: flex;\n  align-items: center;\n  gap: 1em;\n  margin-top: -0.5em;\n  margin-bottom: -0.5em;\n  padding: 1em;\n  background-color: var(--chipmunk--color--white);\n  box-shadow: 0 1px 4px rgba(0 0 0 / 20%);\n  border-radius: 0.25rem;\n  color: var(--chipmunk--color--gray-dark);\n}\n\n.chipmunk__status-icon {\n  flex-shrink: 0;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  height: 1.5em;\n  width: 1.5em;\n  border-radius: 50%;\n  color: var(--chipmunk--color--white);\n\n  &--success {\n    background-color: var(--chipmunk--color--status-success);\n  }\n\n  &--error {\n    background-color: var(--chipmunk--color--status-error);\n  }\n}\n\n.chipmunk__status-content {\n  display: flex;\n  flex-direction: column;\n}\n"],"sourceRoot":""}]);
